
.display {
  width: 100%;
  height: 90vh;
  padding: 1em;
  border-radius: 0.75rem;
  background-color: #1e1e1e;
  border: 0.25em solid #000;
  box-shadow: inset 0 0 0.5em #000;
  overflow-y: auto;
  overflow-x: hidden;
  box-sizing: border-box;
  @media screen and (max-width: 780px) {
    height: 48vh;
  }
}
